html {
  overflow-x: hidden;
  background-color: #fff;
  background-attachment: fixed;
  background-position: center center;
  background-size: cover;
  height: 100%;
}
html body {
  background-color: #fff;
  -webkit-background-size: cover;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: fixed;
  font-family: "Open Sans", Helvetica, Arial, sans-serif, Helvetica, Arial, sans-serif;
  font-weight: 300;
  font-size: 1.4rem;
  line-height: 2.1rem;
  color: #000;
  position: relative;
  z-index: 1;
  min-height: 100%;
  overflow-y: hidden;
}
/***********************************************************************************************/
/* HOME-SHOP SECTION */
/***********************************************************************************************/
.shopHomePage ul,
.shop-products ul,
.footer ul {
  padding-left: 0;
  list-style: none;
  margin-bottom: 0;
}
.shopHomePage ul li,
.shop-products ul li,
.footer ul li {
  position: relative;
  overflow: hidden;
  z-index: 1;
  padding-left: 0;
  padding-right: 0;
}
.shopHomePage ul li figure figcaption img,
.shop-products ul li figure figcaption img,
.footer ul li figure figcaption img {
  width: 100%;
}
.shopHomePage ul li figure .content,
.shop-products ul li figure .content,
.footer ul li figure .content {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  left: 0;
  background: rgba(255, 255, 255, 0.97);
  visibility: hidden;
  opacity: 0;
  text-align: center;
  -webkit-transition-delay: 0;
  transition-delay: 0;
  -webkit-transition-property: all;
  -webkit-transition-duration: 0.4s;
  -webkit-transition-timing-function: ease-in-out;
  transition-property: all;
  transition-duration: 0.4s;
  transition-timing-function: ease-in-out;
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
}
.shopHomePage ul li figure .content .shopHover,
.shop-products ul li figure .content .shopHover,
.footer ul li figure .content .shopHover {
  position: relative;
  top: 45%;
  -webkit-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  -webkit-transform: translateY(-50px);
  -moz-transform: translateY(-50px);
  -o-transform: translateY(-50px);
  -ms-transform: translateY(-50px);
  transform: translateY(-50px);
}
.shopHomePage ul li:hover figure .content,
.shop-products ul li:hover figure .content,
.footer ul li:hover figure .content {
  opacity: 1;
  visibility: visible;
}
.shopHomePage .icon-button,
.shop-products .icon-button,
.footer .icon-button {
  background-color: #fff;
  border-radius: 3rem;
  border: 1px solid #f4f4f4;
  cursor: pointer;
  display: inline-block;
  font-size: 1.2rem;
  height: 3rem;
  line-height: 3rem;
  margin: 0 5px;
  position: relative;
  text-align: center;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  width: 3rem;
}
.shopHomePage .icon-button i,
.shop-products .icon-button i,
.footer .icon-button i {
  background: none;
  color: #000;
  height: 3rem;
  left: -1px;
  line-height: 3rem;
  position: absolute;
  top: 0;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  width: 3rem;
  z-index: 10;
}
.shopHomePage .icon-button span,
.shop-products .icon-button span,
.footer .icon-button span {
  border-radius: 0;
  display: block;
  height: 0;
  left: 50%;
  margin: 0;
  position: absolute;
  top: 50%;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  width: 0;
}
.shopHomePage .icon-button:hover span,
.shop-products .icon-button:hover span,
.footer .icon-button:hover span {
  width: 3.2rem;
  height: 3.2rem;
  border-radius: 3.2rem;
  margin: -15.8px;
}
.shopHomePage .icon-button:hover i,
.shop-products .icon-button:hover i,
.footer .icon-button:hover i {
  color: #fff;
}
.shopHomePage .shopIcon span,
.shop-products .shopIcon span,
.footer .shopIcon span {
  background-color: #bb9b69;
}
.shopHomePage .price,
.shop-products .price,
.footer .price {
  font-size: 2.5rem;
  display: inline-block;
  font-weight: 700;
  color: #333;
  font-family: 'Libre Baskerville', serif;
  padding-bottom: 3.5rem;
}
.shopHomePage .price:before,
.shop-products .price:before,
.footer .price:before {
  content: '';
  width: 1.9rem;
  height: 0.1rem;
  position: absolute;
  background-color: #000;
  -webkit-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
  -o-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  left: 50%;
  top: 4rem;
}
.shopHomePage .proTitle a,
.shop-products .proTitle a,
.footer .proTitle a {
  display: block;
  font-size: 1.8rem;
  text-transform: uppercase;
  color: #5f5e5e;
  font-weight: 600;
  margin-bottom: 0.5rem;
  text-decoration: none;
}
.shopHomePage .proTitle a:hover,
.shop-products .proTitle a:hover,
.footer .proTitle a:hover {
  color: #bb9b69;
}
.shopHomePage .shopIcon,
.shop-products .shopIcon,
.footer .shopIcon {
  margin-top: 1rem;
}
.shopHomePage .product,
.shop-products .product,
.footer .product {
  color: #adacac;
  font-family: 'Karla', sans-serif;
  font-size: 1.1rem;
  font-weight: 600;
}
/***********************************************************************************************/
/* END HOME-SHOP SECTION */
/***********************************************************************************************/
/***********************************************************************************************/
/* START SINGLE SHOP SECTION */
/***********************************************************************************************/
.productDetails nav a {
  text-decoration: none;
  font-weight: 600;
  font-family: 'Karla', sans-serif;
  font-size: 1.3rem;
  color: #919191;
